<template>
  <a class="repo-item" :href="repo.html_url">
  <div class="repo-content">
    <div class="repo-title">
      <span class="text-holder-wrapper" v-text="repo.name">
      </span>
    </div>
    <div class="repo-homepage" v-if="repo.homepage">
      Project site: <a :href="repo.homepage" v-text="repo.homepage"></a>
    </div>
    <div class="repo-desc">
      <span class="text-holder-wrapper" v-text="repo.description">
      </span>
    </div>
    <div class="repo-info">
      <div class="repo-language">
        <span class="text-holder-wrapper" v-text="repo.language">
        </span>
      </div>
      <div class="repo-stats">
        <svg aria-hidden="true" class="octicon octicon-eye" height="16" version="1.1" viewBox="0 0 16 16" width="16"><path fill-rule="evenodd" d="M8.06 2C3 2 0 8 0 8s3 6 8.06 6C13 14 16 8 16 8s-3-6-7.94-6zM8 12c-2.2 0-4-1.78-4-4 0-2.2 1.8-4 4-4 2.22 0 4 1.8 4 4 0 2.22-1.78 4-4 4zm2-4c0 1.11-.89 2-2 2-1.11 0-2-.89-2-2 0-1.11.89-2 2-2 1.11 0 2 .89 2 2z"></path></svg><p v-text="repo.watchers_count"></p>
        <svg aria-hidden="true" class="octicon octicon-star" height="16" version="1.1" viewBox="0 0 14 16" width="14"><path fill-rule="evenodd" d="M14 6l-4.9-.64L7 1 4.9 5.36 0 6l3.6 3.26L2.67 14 7 11.67 11.33 14l-.93-4.74z"></path></svg><p v-text="repo.stargazers_count"></p>
        <svg aria-hidden="true" class="octicon octicon-repo-forked" height="16" version="1.1" viewBox="0 0 10 16" width="10"><path fill-rule="evenodd" d="M8 1a1.993 1.993 0 0 0-1 3.72V6L5 8 3 6V4.72A1.993 1.993 0 0 0 2 1a1.993 1.993 0 0 0-1 3.72V6.5l3 3v1.78A1.993 1.993 0 0 0 5 15a1.993 1.993 0 0 0 1-3.72V9.5l3-3V4.72A1.993 1.993 0 0 0 8 1zM2 4.2C1.34 4.2.8 3.65.8 3c0-.65.55-1.2 1.2-1.2.65 0 1.2.55 1.2 1.2 0 .65-.55 1.2-1.2 1.2zm3 10c-.66 0-1.2-.55-1.2-1.2 0-.65.55-1.2 1.2-1.2.65 0 1.2.55 1.2 1.2 0 .65-.55 1.2-1.2 1.2zm3-10c-.66 0-1.2-.55-1.2-1.2 0-.65.55-1.2 1.2-1.2.65 0 1.2.55 1.2 1.2 0 .65-.55 1.2-1.2 1.2z"></path></svg><p v-text="repo.forks"></p>
      </div>
    </div>
  </div>
</a>
</template>

<script type="text/javascript">
export default {
  props: {
    repo: {
      type: Object,
      required: true
    }
  }
};
</script>
<style lang="stylus">
.repo-item
  border-radius 6px
  border 0.5px solid #e5e5e5
  margin 11px 0
  display block
  text-decoration none !important
  overflow hidden
  color #5c6877

.repo-content
  padding 15px
  background-color #f2f2f2
  .repo-title
    font-size 18px
  .repo-desc, .repo-homepage, .repo-language
    font-size 14px
    word-wrap break-word
  .repo-desc, .repo-homepage
    margin-top 5px
  .repo-info
    display -ms-flexbox
    display flex
    -ms-flex-pack justify
    justify-content space-between
    margin-top 15px

.text-holder-wrapper
  display block

.repo-stats
  display inline-flex
  p
    margin 0 15px
    line-height normal

.repo-item svg
  fill #5c6877
</style>


